@media (max-width: 767px) {
  .navigation--opened {
    position: fixed;
    z-index: 3;
    top: 0;
    right: 0;
    min-width: 285px;
    height: 100vh;
    padding: 70px 25px 20px;
    background-color: #f9fbfd; } }

@media (max-width: 767px) {
  .navigation--opened .navigation__logo {
    position: absolute;
    top: 7px;
    left: 17px;
    display: block; } }

.navigation--opened .navigation__toggle::before, .navigation--opened .navigation__toggle::after {
  position: absolute;
  z-index: 2;
  top: 40px;
  right: 27px;
  width: 18px;
  height: 2px;
  content: "";
