@media (max-width: 767px) {
  .navigation--closed .navigation__logo {
    display: none; } }

.navigation--closed .navigation__toggle::before {
  position: absolute;
  top: 23px;
  right: 15px;
  width: 24px;
  height: 2px;
  content: "";
