.card {
  position: relative;

  min-width: 270px;
  min-height: 241px;
  padding: 82px 10px 30px 30px;

  background-color: $color-alice-blue;

  @include vp-1023 {
    min-width: 206px;
    min-height: 310px;
    padding: 73px 16px 6px 20px;
  }

  @include vp-767 {
    min-width: 250px;
    min-height: 180px;
  }
}

.card h3 {
  font-size: 18px;
  font-weight: 500;
  line-height: 27px;

  margin-top: 0;
  margin-bottom: 8px;

  @include vp-1023 {
    line-height: 25px;
  }

  @include vp-767 {
    max-width: 79%;
    margin-bottom: 5px;
  }
}

.card p {
  font-size: 14px;
  line-height: 20px;

  margin-top: 0;
}

.card svg {
  position: absolute;
  top: 30px;
  left: 30px;
}

.card--liner svg {
  @include vp-1023 {
    top: 26px;
    left: 20px;

    width: 21px;
    height: 26px;
  }
}

.card--all-inclusive svg {
  top: 38px;
  left: 30px;

  @include vp-1023 {
    top: 30px;
    left: 20px;

    width: 33px;
    height: 19px;
  }
}

.card--tour svg {
  top: 38px;
  left: 30px;

  @include vp-1023 {
    top: 33px;
    left: 20px;

    width: 38px;
    height: 13px;
  }
}
