.header {
  position: absolute;

  width: 100%;
  padding-top: 26px;
  padding-bottom: 12px;

  color: $color-alice-blue;
  background-color: transparent;

  @include vp-1023 {
    padding-top: 12px;
  }
}

.header__container {
  display: grid;

  grid-template-columns: 1fr 5fr;

  @include vp-767 {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}

.header__navigation {
  margin-left: auto;
}

.header__logo {
  margin-top: 7px;

  @include vp-1023 {
    margin-top: 17px;
  }

  @include vp-767 {
    width: 42px;
    height: 22px;
    margin-top: 12px;
  }
}

.header__logo--hidden {
  visibility: hidden;
}

.header--no-js {
  @include vp-767 {
    position: static;

    background-color: $color-alice-blue;
  }
}

.header--no-js .header__logo {
  @include vp-767 {
    display: none;
  }
}

.header__navigation--open {
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 43, 102, 0.5);
  position: fixed;
  z-index: 3;
  top: 0;
  left: 0;
}
